import {homeList} from '../../../common/cloudfun.js'
export default{
	name: "content",
	props:{
		tab:Array
	},
	data(){
		return{
			num: 0
		}
	},
	methods:{
		clickTabs(index,nav){
			this.num = index;
			console.log(nav)
			//点击切换loading状态显示
			let loading = true
			this.$store.commit('naumuat',loading)
			//请求数据库
			homeList(nav)
			.then((res)=>{
				console.log(res)
				//vuex传值
				let listdata = res.data
				this.$store.commit('listmuta',listdata)
			})
			.catch((err)=>{
				console.log(err)
			})
		}
	},
	
}
